/* Root variables for dark mode with pink, purple, blue palette */
:root {
  --bg-dark: #1a1a2e; /* Deep dark blue-gray */
  --text-color: #e0e0e0; /* Light gray for readability */
  --primary-pink: #ff69b4; /* Hot pink */
  --primary-purple: #8a2be2; /* Vibrant purple */
  --primary-blue: #1e90ff; /* Bright blue */
  --hover-pink: #ff1493; /* Deeper pink */
  --hover-purple: #6a0dad; /* Darker purple */
  --hover-blue: #4169e1; /* Royal blue */
  --accent-bg: #2a2a40; /* Slightly lighter dark for contrast */
  --border-color: rgba(255, 255, 255, 0.1); /* Subtle white border */
  --shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  --spacing-sm: 10px;
  --spacing-md: 20px;
  --spacing-lg: 30px;
  --border-radius: 10px;
  --transition: all 0.3s ease-in-out;
}

/* Base container styles */
.container {
  font-family: 'Poppins', Arial, sans-serif; /* Modern, stylish font */
  margin: var(--spacing-md);
  color: var(--text-color);
  background: var(--bg-dark);
  min-height: 100vh; /* Full-screen feel */
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden; /* Contain animations */
}

/* Section styles with gradient and glow */
.section {
  margin-bottom: var(--spacing-lg);
  padding: var(--spacing-md);
  background: linear-gradient(135deg, var(--accent-bg), #222238); /* Subtle gradient */
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow), 0 0 10px rgba(138, 43, 226, 0.2); /* Purple glow */
  transition: var(--transition);
}

.section:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.6), 0 0 15px rgba(255, 105, 180, 0.3); /* Pink glow */
}

/* List styles */
.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.list-item {
  padding: var(--spacing-sm) var(--spacing-md);
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.03); /* Subtle overlay */
  transition: var(--transition);
}

.list-item:last-child {
  border-bottom: none;
}

.list-item:hover {
  background: linear-gradient(90deg, rgba(255, 105, 180, 0.1), rgba(30, 144, 255, 0.1)); /* Pink-to-blue fade */
  transform: scale(1.02); /* Slight zoom */
}

/* Input styles with neon effect */
input[type="text"] {
  padding: var(--spacing-sm);
  margin-bottom: var(--spacing-sm);
  border: 1px solid var(--primary-purple);
  border-radius: var(--border-radius);
  width: clamp(200px, 50vw, 300px);
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-color);
  transition: var(--transition);
}

input[type="text"]:focus {
  border-color: var(--primary-pink);
  box-shadow: 0 0 8px var(--primary-pink), 0 0 12px rgba(255, 105, 180, 0.5); /* Neon pink glow */
  outline: none;
}

/* Button styles with gradient and animation */
button {
  padding: var(--spacing-sm) var(--spacing-md);
  background: linear-gradient(45deg, var(--primary-purple), var(--primary-blue)); /* Purple-to-blue gradient */
  color: white;
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  margin-left: var(--spacing-sm);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  transition: var(--transition);
}

button::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.4s ease, height 0.4s ease;
}

button:hover::after {
  width: 200%;
  height: 200%;
}

button:hover {
  background: linear-gradient(45deg, var(--hover-purple), var(--hover-blue));
  transform: translateY(-2px);
  box-shadow: 0 0 10px var(--primary-blue); /* Blue glow */
}

button:active {
  transform: translateY(1px);
}

/* Edit form styles */
.edit-form {
  margin-top: var(--spacing-md);
  padding: var(--spacing-md);
  background: linear-gradient(135deg, #2a2a40, #1e1e2e);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow), 0 0 8px rgba(138, 43, 226, 0.2); /* Purple glow */
  transition: var(--transition);
}

.edit-form input[type="text"] {
  width: clamp(180px, 40vw, 250px);
}

.edit-form button {
  margin-top: var(--spacing-sm);
}

/* Responsive design */
@media (max-width: 768px) {
  .container {
    margin: var(--spacing-sm);
  }

  .section,
  .edit-form {
    padding: var(--spacing-sm);
  }

  input[type="text"],
  .edit-form input[type="text"] {
    width: 100%;
  }

  button {
    width: 100%;
    margin-left: 0;
    margin-top: var(--spacing-sm);
  }
}

/* Animations */
@keyframes fadeInGlow {
  0% {
    opacity: 0;
    transform: translateY(15px);
    box-shadow: none;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    box-shadow: var(--shadow), 0 0 10px rgba(138, 43, 226, 0.2);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 5px var(--primary-pink);
  }
  50% {
    box-shadow: 0 0 15px var(--primary-pink), 0 0 20px rgba(255, 105, 180, 0.5);
  }
  100% {
    box-shadow: 0 0 5px var(--primary-pink);
  }
}

.section,
.edit-form {
  animation: fadeInGlow 0.6s ease-out forwards;
}

button:hover {
  animation: pulse 1.5s infinite ease-in-out; /* Pulsing glow effect */
}